import UserItem from '../../../components/UserItem/UserItem.vue'
import UserModel from '@/models/user.js'
const userModel = new UserModel()
export default {
	data() {
		return {
			userData: {
				// name: '张三'
			},
			user:{}
		}
	},
	components: {
		UserItem,
	},
	// onLoad(){
	// 	this.getUserInfo()
	// },
	mounted(){
		this.getUserInfo()
	},
	methods: {
		getUserInfo(){
			userModel.getUserInfo().then(res=>{
				//数据绑定
				const { code, message, data } = res.data
				if(code === '0'){
					this.user = data
					this.userData = data
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.showToast({
					icon: 'none',
					title:'获取用户信息失败'
				})
			})
		},
	}
}
